import React, { useEffect, useState, useRef } from 'react'
import FirebaseInit from "../../helper/config/FirebaseInit"
import { colorRand } from '../../helper/config/Colors'

const Whises = (guestName) => {

    var dateformat = require('dateformat')
    const [chats, setChats] = useState([])
    const [senderName, setSenderName] = useState("")
    const [message, setMessage] = useState("")
    const [showChats, setShowChats] = useState(false)

    const messageRef = useRef(null)
    const [autoScroll, setAutoScroll] = useState(false)

    useEffect(() => {
        setSenderName(guestName.guestName)
        getShowChats()
        getChats()
    }, [])

    useEffect(() => {
        if(autoScroll){
            messageRef?.current?.scrollIntoView({ behavior: "smooth" })
        }
    }, [chats?.length])

    function getShowChats() {
        let ref = FirebaseInit.database().ref('/invitation/manca-marni/showChats')
        ref.on('value', snapshot => {
            setShowChats(snapshot.val() ?? false)
        })
    }

    function getChats() {
        let ref = FirebaseInit.database().ref('/invitation/manca-marni/whises')
        ref.on('value', snapshot => {
            let chats = snapshot.val()
            let newState = []
            for (let chat in chats) {
                newState.push({
                    id: chat,
                    color: chats[chat].color,
                    from: chats[chat].from,
                    message: chats[chat].message,
                    time: chats[chat].time
                })
            }

            setChats(newState)
        })
    }

    function handleSend() {
        if (senderName.length > 3 && message.length > 3) {
            let ref = FirebaseInit.database().ref('/invitation/manca-marni/whises')
            ref.child(Date.now()).set({
                color: colorRand,
                message: message,
                from: senderName,
                time: dateformat(Date.now(), "dd mmm yyyy, hh:MM")
            })

            setMessage("")
        }else{
            if(senderName.length <= 3){
                alert("Isi nama kamu dulu yah :)")
            }else if(message.length <= 3){
                alert("Isi pesan kamu duu yah :)")
            }
        }
    }

    if(!showChats){
        return <></>
    }

    return (
        <div className="whises-container">
            <h1 className="title" data-aos="fade-up">Kirimkan Pesan</h1>
            <div className="info" data-aos="fade-up">
                <p className="deskripsi" >Ucapan selamat dan kebahagiaan bisa dari mana saja.
                    Tanpa berjabat tangan atau berpelukan hangat
                    masih ada simpul senyum dan doa terbaik yang kami harapkan
                </p>
            </div>
            <div className="message-history">
                {
                    chats.map(e => {
                        return <div className='incoming-message'>
                            <span className="sender" style={{ color: e.color }}>{e.from}</span>
                            <div className='message'>
                                <p>{e.message}</p>
                                <span className="time-date">{e.time}</span>
                            </div>
                        </div>
                    })
                }
                <div ref={messageRef}/>
            </div>

            <div className='type-message'>
                <input
                    type="text"
                    className="sender-name"
                    onChange={(e) => setSenderName(e.target.value)}
                    value={senderName}
                    placeholder="Nama Pegirim" />

                <textarea
                    maxLength={255}
                    placeholder="Ketik Pesanmu"
                    value={message}
                    onChange={e => { 
                        setMessage(e.target.value)
                        if(!autoScroll){
                            setAutoScroll(true)
                        }
                     }}
                    className='message-text'
                />

                <button
                    className="send-button"
                    type="button"
                    onClick={handleSend}
                >Kirim</button>
            </div>
        </div>
    )
}

export default Whises
